  font-family: Dosis;
  margin-left: 5px;
  color: #707f8f;
}

.NoHassle span:not(:last-child) {
  margin-right: 20px;
}
.NoHassle span {
  white-space: nowrap;
}

.Header-form-input {
  box-sizing: border-box;
  color: #162637;
  font-family: Dosis;
  line-height: 22px;
}

.Header-form-input,
.Header-form-button {
  font-size: 18px;
  padding: 20px 32px;
  width: 100%;
}

.Header-form-button {
  background-color: #324168;
  color: #fff;
  font-family: Dosis;
  font-weight: 600;
  line-height: 27px;
  transition: ease-in-out 200ms background-color;
}

.Header-form-button:hover {
  background-color: #485d94;
}

input {
  border: 0;
  outline: none;
}

button {
  background-color: transparent;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 0;
}

button,
input {
  overflow: visible;
}

.zio {
  animation: bounce 3s infinite ease-in-out;
  -webkit-animation: bounce 3s infinite ease-in-out;
  -o-animation: bounce 3s infinite ease-in-out;
  -ms-animation: bounce 3s infinite ease-in-out;
